












































































import {Component, Vue, Watch} from "vue-property-decorator";
import {GetAllUsersDocument, GetUsersAggregateDocument} from "~/gql";
import UserActions from "~/components/users/userActions.vue";

@Component({
    components: {
        UserActions
    },
    layout: 'console',
    apollo: {
        users: {
            query: GetAllUsersDocument,
            variables() {
                return {
                    limit: this.limit,
                    iLike: `%${this.search}%`,
                    offset: (this.page - 1) * this.limit
                }
            }
        },
        userAggregate: {
            query: GetUsersAggregateDocument,
            variables() {
                return {
                    iLike: `%${this.search}%`
                }
            }
        }
    }
})
export default class AllCustomers extends Vue {
    private search = ''
    private limit = 50
    private page = 1

    private users

    //table
    private gridOptions: any = {};
    private gridApi: any = null;
    private columnDefs = [
        {
            headerName: 'First Name',
            filter: false,
            field: 'firstName'
        },
        {
            headerName: 'Last Name',
            filter: false,
            field: 'lastName'
        },
        {
            headerName: 'Actions',
            cellRendererFramework: 'UserActions'
        }
    ]
    private defaultColDef = {
        sortable: true,
        editable: false,
        resizable: true,
        suppressMenu: true
    };

    @Watch('users')
    onChangUsers() {
        console.log(this.users)
    }

    onGridReady() {
        this.gridApi = this.gridOptions!.api;
        this.gridApi!.sizeColumnsToFit();
    }

}
